"use client";

import { useState, useTransition } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import { Upload, Loader2, Sparkles, CheckCircle } from 'lucide-react';

import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Label } from '@/components/ui/label';
import { useToast } from '@/hooks/use-toast';
import { handleResumeAnalysis } from '@/lib/actions';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';

const applicationSchema = z.object({
  name: z.string().min(1, 'Name is required'),
  email: z.string().email('Invalid email address'),
  skills: z.string().optional(),
  coverLetter: z.string().optional(),
});

type ApplicationFormData = z.infer<typeof applicationSchema>;

export function ApplicationForm() {
  const { toast } = useToast();
  const [isAnalyzing, startAnalyzing] = useTransition();
  const [analysisComplete, setAnalysisComplete] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);

  const form = useForm<ApplicationFormData>({
    resolver: zodResolver(applicationSchema),
    defaultValues: { name: '', email: '', skills: '', coverLetter: '' },
  });

  const onFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    if (file.type !== 'application/pdf') {
      toast({ variant: 'destructive', title: 'Invalid File Type', description: 'Please upload a PDF file.' });
      return;
    }

    const reader = new FileReader();
    reader.onload = (event) => {
      const dataUri = event.target?.result as string;
      setAnalysisComplete(false);
      startAnalyzing(async () => {
        const result = await handleResumeAnalysis(dataUri);
        if (result.error) {
          toast({ variant: 'destructive', title: 'Analysis Failed', description: result.error });
        } else if (result.data) {
          const { skills } = result.data;
          form.setValue('skills', skills.join(', '));
          setAnalysisComplete(true);
          toast({ title: 'Resume Analyzed!', description: 'Your skills have been pre-filled.' });
        }
      });
    };
    reader.readAsDataURL(file);
  };

  const onSubmit = async (data: ApplicationFormData) => {
    setIsSubmitting(true);
    await new Promise(resolve => setTimeout(resolve, 2000));
    setIsSubmitting(false);
    toast({ title: 'Application Submitted!', description: 'Your application has been sent successfully. Good luck!' });
    form.reset();
    setAnalysisComplete(false);
    // In a real app, you would close the dialog here.
  };
  
  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(onSubmit)} className="grid gap-4 py-4">
        <div className="space-y-2">
          <Label htmlFor="resume">Upload Resume (PDF)</Label>
          <div className="relative">
            <Input id="resume" type="file" onChange={onFileChange} accept=".pdf" className="pr-12 file:text-primary file:font-semibold" disabled={isAnalyzing} />
            <div className="absolute inset-y-0 right-0 flex items-center pr-3 pointer-events-none">
              {isAnalyzing ? (
                <Loader2 className="h-5 w-5 animate-spin text-muted-foreground" />
              ) : analysisComplete ? (
                <CheckCircle className="h-5 w-5 text-green-500" />
              ) : (
                <Upload className="h-5 w-5 text-muted-foreground" />
              )}
            </div>
          </div>
          <p className="text-sm text-muted-foreground flex items-center gap-1.5">
            <Sparkles className="h-4 w-4 text-amber-500" />
            AI will extract your skills to pre-fill the form.
          </p>
        </div>

        <FormField
          control={form.control}
          name="name"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Full Name</FormLabel>
              <FormControl>
                <Input placeholder="John Doe" {...field} />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />
        <FormField
          control={form.control}
          name="email"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Email Address</FormLabel>
              <FormControl>
                <Input placeholder="you@example.com" {...field} />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />
        <FormField
          control={form.control}
          name="skills"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Skills (from Resume)</FormLabel>
              <FormControl>
                <Input placeholder="AI-extracted skills will appear here..." {...field} disabled />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />
        <FormField
          control={form.control}
          name="coverLetter"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Cover Letter (Optional)</FormLabel>
              <FormControl>
                <Textarea placeholder="Why are you a good fit for this role?" rows={5} {...field} />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />
        <Button type="submit" className="w-full" disabled={isSubmitting}>
          {isSubmitting && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
          {isSubmitting ? 'Submitting...' : 'Submit Application'}
        </Button>
      </form>
    </Form>
  );
}
